import React from 'react'
import 'react-responsive-carousel/lib/styles/carousel.min.css'
import { Carousel } from 'react-responsive-carousel'

export const SolutionsSlider = () => {

    return (
        <Carousel autoPlay={true} centerMode={true} centerSlidePercentage={90} showIndicators={false} infiniteLoop={true} showStatus={false} transitionTime={1000} stopOnHover={true} showThumbs={false}>
            <div className="df jcc">
                <div className="df card">
                    <img src="./img/technologies/python_logo.png" alt="slide 1"  className="icon" />
                </div>
                <div className="df card">
                    <img src="./img/technologies/yolo_logo.webp" alt="slide 2" className="icon" />
                    {/* <span>Yolo</span> */}
                </div>
                <div className="card">
                    <span>Neural Networks</span>
                </div>
                <div className="card">
                    <span>HTML/CSS/JS</span>
                </div>
                <div className="card">
                    <span>Figma</span>
                </div>
            </div>
        </Carousel>
    )
}


